import { NavLink } from "react-router-dom";
import s from './Navigation.module.css';

const Nav = () => (
    <nav>
        <NavLink to="/" className={({ isActive }) => isActive ? s.active : s.inactive}>Home page</NavLink>
        <NavLink to="/movies" className={({ isActive }) => isActive ? s.active : s.inactive}>Movies page</NavLink>
    </nav>
)

export default Nav;